}
.button{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 19px 26px;

    position: relative;
    width: 100px;
    height: 15px;
    left: 1000px;
    top: auto;
   border-radius:10px;
    text-decoration: none;
    color: rgba(0,0,0,0.9);

    /* Black */

    background: #367dc5;
}
.button:hover{
    color: white;
