}
ul{
    list-style-type: none;
    display: flex;
    margin-top: 26px;
}
ul li{
    margin-left: 20px;
}
.button-haut{
    font-size: 17px;
    height: 35px;
    width: 90px;
    margin-top: 18px;
    border:1px solid black;
    border-radius: 5px;
    background-color: white;
}
button:hover{
    background-color: black;
    color: white;
}
.compteur{
    color: white;
    border-radius: 100%;
