}
.home{
    display: flex;
    margin-right: 50px;
    text-decoration: none;
    color: black;

}
.home:hover{
    text-decoration: none;
    color: blueviolet;
    font-weight: bold;
}
.shop{
    display: flex;
    margin-right: 50px;
