}
.shop:hover{
    text-decoration: none;
    color: blueviolet;
    font-weight: bold;
}
.about{
    text-decoration: none;
    color: black;
}
.about:hover{
    text-decoration: none;
    color: blueviolet;
    font-weight: bold;
}
.Shop{
    width: 68%;
    margin: auto;
    overflow-x: visible;
}
.product-item {
    height: 500px;
    width: 300px;
    border: 0px solid black;
    border-radius: 10px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    margin-bottom: 90px;
    position: relative;
}

.item-cover {
    border-radius: 10px;
    height: 300px;
    width: 300px;
}

.titre {
    text-align: center;
    margin-top: 20px;
    font-size: 20px;
    font-weight: bold;
    color: rgba(20, 19, 19, 0.918);
}

.star {
    text-align: center;
    margin-top: 10px;
}

.str {
    margin-bottom: 15px;
}

.Price {
    text-align: center;
}

.barre {
    text-decoration: line-through;
    color: gray;
}

.button-bas {
    font-size: 17px;
    height: 35px;
    width: 110px;
    margin-top: 18px;
    border: 1px solid black;
    border-radius: 5px;
    background-color: white;
    margin: auto;
    margin-bottom: 30px;
}

.sale {
    position: absolute;
    text-align: center;
    top: 10px;
    right: 10px;
    width: 40px;
    height: 20px;
    background-color: rgba(18, 19, 20, 0.904);
    color: white;
    font-size: 14px;
    border-radius: 5px;
}
.shoping-list{
    width: 100%;
